import _ from 'lodash'

import Table from '../../mixins/table'

import route from './route'

export default {
  name: route.key,

  mixins: [
    Table
  ],

  data() {
    return {
      action: null,
      filter: {
        draw: null,
        date_from: null,
        date_to: null,
        reference_no: null
      },
      route,
    }
  },

  computed: {
    data() {
      return [
        {
          reference_no: '00000017',
          draw: '3D 9pm',
          combination: '456',
          flag: 15.20,
          date_created: '2021-03-26T12:11:50.058Z',
          status: 'Successful'
        }
      ]
    }
  },

  methods: {
    async init() {

    }
  }
}



